<template>
  <!-- 认证审核中 -->
  <el-dialog
    class="custom-dialog"
    width="820px"
    lock-scroll
    append-to-body
    center
    title="数企订购协议"
    :model-value="visible"
    @close="handleClose"
  >
    <div class="agreement">
      <p class="content" v-html="paramsData.agreement"></p>
    </div>
  </el-dialog>
</template>

<script lang="ts" setup>
import { shallowReactive } from "vue";
import { agreementApi } from "@api/common-api";

defineProps<{
  visible: boolean;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean): void;
}>();
const paramsData = shallowReactive({
  agreement: "",
});
// 获取用户购买协议
const getAgreement = () => {
  const params = {
    type: 0,
  };
  agreementApi.getData(params).then((res) => {
    paramsData.agreement = agreementApi.handleData(res.data);
  });
};
const handleClose = () => {
  emits("update:visible", false);
};
getAgreement();
</script>

<style lang="scss" scoped>
.agreement {
  height: 60vh;
  overflow: auto;
  .content {
    font-size: 16px;
    line-height: 28px;
  }
}
</style>
