<template>
  <div class="main-container">
    <div class="product-detail">
      <div class="name-function">
        <h6 class="name">{{ detailInfo?.productName }}</h6>
        <p class="function">{{ detailInfo?.productDescribe }}</p>
      </div>
      <div class="buy-params-box">
        <div class="params-container">
          <div class="product-img">
            <img
              :src="detailInfo?.procudtPrimaryImg"
              @error="handleImgError"
              alt=""
            />
          </div>
          <div class="level-content">
            <div class="level">
              <span class="label">服务商级别</span>
              <ul class="level-list">
                <li
                  :class="[
                    'item',
                    paramsData.activeLevelIndex === index ? 'active' : '',
                  ]"
                  v-for="(item, index) in levelList"
                  :key="index"
                  @click="handlerLevel(item, index)"
                >
                  {{ item.label }}
                </li>
              </ul>
            </div>
            <div class="level">
              <span class="label">代理周期</span>
              <el-select
                placeholder="请选择代理周期"
                v-model="paramsData.timeLength"
              >
                <el-option
                  v-for="item in detailInfo?.timeList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </div>
            <div class="level" v-if="!['ec-line'].includes(systemName)">
              <span class="label">服务协议</span>
              <el-checkbox v-model="paramsData.agreementStatus"
                >我已阅读并同意</el-checkbox
              >
              <span
                class="agreement"
                @click="paramsData.agreementVisible = true"
                >《数企订购协议》</span
              >
            </div>
          </div>
        </div>
        <div class="buy-container">
          <p class="amount"><span>&yen;</span>{{ paramsData.amount }}</p>
          <el-button
            type="primary"
            color="#0056ff"
            :disabled="paramsData.existBuyFlag"
            @click="handlerBuy"
            >{{ paramsData.existBuyFlag ? "已购买服务商" : "购买服务商" }}
          </el-button>
        </div>
      </div>
      <div class="product-params-box">
        <h6 class="title">·&nbsp;系列产品介绍</h6>
        <el-table
          :data="detailInfo?.appList"
          border
          style="width: 100%"
          :span-method="spanMerge"
        >
          <el-table-column
            :label="['ec-line'].includes(systemName) ? '系列名称' : '数企系列'"
          >
            <template #default>{{ detailInfo?.productName }}</template>
          </el-table-column>
          <el-table-column prop="appName" label="产品名称"></el-table-column>
          <el-table-column prop="groupNames" label="规格"></el-table-column>
          <el-table-column label="代理级别">
            <template #default>{{ paramsData.activeLevelLabel }}</template>
          </el-table-column>
          <el-table-column prop="marketPrice" label="市场价"></el-table-column>
        </el-table>
        <img :src="detailInfo?.procudtDetailImg" alt="" />
      </div>
    </div>
  </div>
  <!--  认证弹框 -->
  <CertificationStatusDialog
    v-model:visible="paramsData.certificationVisible"
    :checkAdvice="paramsData.checkAdvice"
    :certificationStatus="checkStatus"
  ></CertificationStatusDialog>
  <!--  购买协议 -->
  <PurchaseAgreementDialog
    v-model:visible="paramsData.agreementVisible"
    :agreement="paramsData.agreement"
  ></PurchaseAgreementDialog>
</template>
<script lang="ts" setup>
import { ref, shallowReactive } from "vue";
import { ElMessage } from "element-plus";

import { LocalStorage } from "jz-tool-lib";
import { useVueRouter } from "@assets/useHook";
import { systemName, handleImgError } from "@assets/common";

import {
  parentAgencyLevelApi,
  productDetailApi,
  existBuyApi,
} from "@api/market-service-api";

import PurchaseAgreementDialog from "@components/common/purchase-agreement-dialog.vue";
import CertificationStatusDialog from "@components/common/certification-status-dialog.vue";

defineOptions({
  name: "marketService.applicationCenter.detail",
});

const { routeQuery, goPage } = useVueRouter();
const checkStatus = LocalStorage.getItem("bd_checkStatus");
const levelList = ref<unknown[]>([]);
const detailInfo = ref<Record<string, any>>({});
const paramsData = shallowReactive({
  activeLevelIndex: -1,
  activeLevelLabel: "",
  activeLevelValue: -1,
  existBuyFlag: false,
  agreementVisible: false,
  certificationVisible: false,
  agreementStatus: false,
  checkAdvice: "",
  amount: 0,
  timeLength: "",
});

// 获取详情
const productDetail = async () => {
  try {
    const params = {
      productId: routeQuery.value.productId,
    };
    const { data } = await productDetailApi.getData(params);
    detailInfo.value = productDetailApi.handleData(data);
    paramsData.timeLength = detailInfo.value?.timeLength;
    await parentAgencyLevel();
  } catch (e) {
    console.log(e);
  }
};
// 判断当前用户是否购买过
const existBuy = async () => {
  try {
    const params = {
      productId: routeQuery.value.productId,
    };
    const { data } = await existBuyApi.getData(params);
    paramsData.existBuyFlag = existBuyApi.handleData(data);
  } catch (e) {
    console.log(e);
  }
};
// 获取父级代理商级别
const parentAgencyLevel = async () => {
  const { data } = await parentAgencyLevelApi.getData();
  const { levelList: list } = parentAgencyLevelApi.handleData(data);
  levelList.value = list;
  handlerLevel(levelList.value[0], 0);
};
// 选择购买服务商级别
const handlerLevel = (item: any, index: number) => {
  paramsData.activeLevelIndex = index;
  paramsData.activeLevelLabel = item.label;
  paramsData.activeLevelValue = item.value;
  paramsData.amount = getAgentPrice(item.value, detailInfo.value);
};
// 获取代理价格
const getAgentPrice = (agentLevel, info) => {
  switch (agentLevel) {
    case 1:
      return info.highLevelAgentPrice;
    case 2:
      return info.midLevelAgentPrice;
    case 3:
      return info.lowLevelAgentPrice;
  }
};
// 购买服务商
const handlerBuy = () => {
  if (checkStatus !== 2) {
    const checkAdvice = LocalStorage.getItem("bd_checkAdvice");
    if (checkAdvice) {
      paramsData.checkAdvice = checkAdvice;
    }
    paramsData.certificationVisible = true;
    return;
  }
  if (!paramsData.agreementStatus && !["ec-line"].includes(systemName)) {
    ElMessage.error("请阅读并勾选数企订购协议");
    return;
  }
  goPage(
    `/payment/buy?productId=${detailInfo.value?.productId}&amount=${paramsData.amount}&agencyLevel=${paramsData.activeLevelValue}&durationYear=${detailInfo.value?.timeLength}`
  );
};
// 合并单元格
const spanMerge = ({ rowIndex, columnIndex }) => {
  if (columnIndex === 0 || columnIndex === 3) {
    if (rowIndex % detailInfo.value?.appList.length === 0) {
      return {
        rowspan: detailInfo.value?.appList.length,
        colspan: 1,
      };
    } else {
      return {
        rowspan: 0,
        colspan: 0,
      };
    }
  }
};
existBuy();
productDetail();
</script>

<style lang="scss" scoped>
.product-detail {
  width: 1200px;
  margin: 0 auto;
}

.name-function {
  margin: 30px 0;

  .name {
    font-size: 28px;
    font-weight: 600;
    color: #242934;
    line-height: 1;
    margin-bottom: 12px;
  }

  .function {
    font-size: 14px;
    color: #9ca6b9;
    line-height: 1;
  }
}

.buy-params-box {
  background: #ffffff;
  border-radius: 8px;
}

.params-container {
  display: flex;
  padding: 40px 40px 35px;
  .product-img {
    width: 200px;
    height: 200px;
    box-shadow: 0 0 10px 0 rgba(100, 107, 122, 0.08);
    border-radius: 0 0 4px 4px;
    img {
      width: 100%;
      height: 100%;
    }
    img[src=""] {
      display: none;
    }
  }
  .level-content {
    flex: 0 0 auto;
    margin-left: 20px;
  }
  .level {
    display: flex;
    align-items: center;
    margin-bottom: 24px;

    .label {
      width: 105px;
      font-size: 14px;
      color: #242934;
      line-height: 1;
    }
    .agreement {
      font-size: 14px;
      color: #0056ff;
      cursor: pointer;
      line-height: 1;
    }
    .level-list {
      display: flex;

      .item {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 160px;
        height: 40px;
        font-size: 14px;
        color: #212325;
        cursor: pointer;
        line-height: 20px;
        margin-right: 12px;
        background-color: #f5f7fb;
        border-radius: 4px;
        border: 1px solid #f5f7fb;

        &.active {
          color: #0056ff;
          border-color: #0056ff;
        }
      }
    }

    .sets {
      font-size: 14px;
      color: #212325;
    }
  }
}

.buy-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 28px 140px;
  background: #ffffff;
  box-shadow: 0 0 20px 0 rgba(100, 107, 122, 0.08);
  border-radius: 0 0 8px 8px;

  .amount {
    font-size: 28px;
    font-weight: 600;
    color: #f12525;
    line-height: 1;
    margin-bottom: 16px;

    span {
      font-size: 18px;
      margin-right: 2px;
    }
  }
}

.product-params-box {
  margin-top: 28px;
  padding: 36px 28px;
  background: #ffffff;
  border-radius: 8px;

  .title {
    font-size: 18px;
    color: #242934;
    line-height: 1;
    margin-bottom: 28px;
  }
  img {
    width: 100%;
    margin-top: 20px;
  }
}

.el-select {
  width: 500px;
}

:deep(.el-checkbox) {
  &.is-checked {
    .el-checkbox__label {
      color: #0056ff;
    }

    .el-checkbox__inner {
      background-color: #0056ff;
      border-color: #0056ff;
    }
  }

  .el-checkbox__input.is-focus .el-checkbox__inner {
    border-color: #0056ff;
  }
}

:deep(.el-select) {
  .el-input__inner:focus,
  .el-input.is-focus .el-input__inner {
    border-color: #0056ff;
  }
}
</style>
